













body{
  padding: 0px;
  margin:0px;
}
h1{
  color: #fff;
  font-size: 22px;
  text-align: center;
  line-height: 22px;
}
.app-container{
  position: absolute;
  width: 100%;
  height: 100%;
  background: -webkit-gradient(linear, left top, left bottom, from(#6586ff), to(#23ffc2));
  background: linear-gradient(#6586ff, #23ffc2);
}
.data{
  display: grid;
  grid-template-rows:  30px 30px 1fr;
  grid-row-gap: 20px;
  width: 100%;
  height: 100%;
  padding: 26px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.pack{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: rgba(0,0,0,0.1);
  padding: 5px;
  border-radius: 5px;
}
.total{
  margin-left: 10px;
  display: block;
  height: 100%;
  width: 400px;
  background-color: #a3a3a3;
  float: left;
}
.total .cur{
  display: block;
  height: 100%;
  background-color: green;
}
.title{
  color: #333333;
  font-weight: bold;
  display: block;
  float: left;
  font-size: 20px;
  line-height: 20px;
}
.circle{
  margin-left: 10px;
  float: left;
  display: block;
  width: 20px;
  height: 20px;
  border-radius: 10px;
}
.green{
  background-color: rgb(0, 255, 0);
}
.red{
  background-color: rgb(255, 0, 0)
}
.curs{
  cursor: pointer;
  margin-left: 15px;
  background-color: #000;
  text-decoration: none;
  color: #fff;
  padding: 4px 8px 4px 8px;
  border-radius: 4px;
  height: 20px;
  -moz-user-select: none; /*火狐*/
  -webkit-user-select: none; /*webkit浏览器*/
  -ms-user-select: none; /*IE10*/ /*早期浏览器*/
  user-select: none;
}
.curs:active{
  background-color:rgb(50, 50, 50);
}
.user{
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: 100px;
  grid-gap: 5px;
}
.user .item{
  width: 100%;
  height: 100px;
  background-color: rgba(255,255,255,0.2);
  padding: 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 5px;
  position: relative;
  -moz-user-select: none; /*火狐*/
  -webkit-user-select: none; /*webkit浏览器*/
  -ms-user-select: none; /*IE10*/ /*早期浏览器*/
  user-select: none;
}
.user .item:hover{
  background-color: rgba(255,255,255,0.3);
}
.user .item .row{
  height: 20px;
  width: 100%;
  font-size: 16px;
  line-height: 20px;
}
.user .item .row .key{
  display: block;
  float: left;
  width: 50%;
  text-align: left;
}
.user .item .row .value{
  display: block;
  float: left;
  width: 50%;
  text-align: right;
}
.user .item .tools{
  position: absolute;
  bottom: 5px;
  right: 5px;
  height: 20px;
  line-height: 20px;
  font-size: 14px;
  color: #001b7c;
  cursor: pointer;
}

body{
  padding: 0px;
  margin:0px;
}
h1{
  color: #fff;
  font-size: 22px;
  text-align: center;
  line-height: 22px;
}
.app-container{
  position: absolute;
  width: 100%;
  height: 100%;
  background: -webkit-gradient(linear, left top, left bottom, from(#6586ff), to(#23ffc2));
  background: linear-gradient(#6586ff, #23ffc2);
}
.login{
  border-radius: 10px;
  background: -webkit-gradient(linear,left top, left bottom,from(#23ffc2),to(#6586ff));
  background: linear-gradient(#23ffc2,#6586ff);
  width: 300px;
  position: absolute;
  right: 50px;
  top: 20%;
  padding: 50px;
}
.login input{
  outline: gray;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  width: 100%;
  height: 36px;
  border-radius: 12px;
  margin-bottom: 10px;
  padding: 6px;
  padding-left: 12px;
  border: 0px;
}
.login input :focus{
  border: 0px;
  background-color: red;
}
.login .submit{
  font-weight: normal;
  text-align: center;
  display: block;
  width: 100%;
  float: right;
  background: -webkit-gradient(linear,left top, left bottom,from(#fff),to(rgb(42, 253, 253)));
  background: linear-gradient(#fff,rgb(42, 253, 253));
  color: #7a8eff;
  border-radius: 15px;
  height: 36px;
  font-size: 18px;
  line-height: 36px;
  border: gray solid 1px;
  cursor:pointer;
}
.login .submit:hover {
  background: -webkit-gradient(linear,left top, left bottom,from(#fff),to(rgb(60, 243, 243)));
  background: linear-gradient(#fff,rgb(60, 243, 243));
}
.login .submit:active{
  background: -webkit-gradient(linear,left top, left bottom,from(#fff),to(rgb(30, 235, 235)));
  background: linear-gradient(#fff,rgb(30, 235, 235));
}

body{
