#cim{
  position:absolute;
  width:100%;
  height: 100%;
}
.action{
  width: 200px;
  padding: 20px;
  border-radius: 10px;
  right: 50px;
  top: 50px;
  position: absolute;
  z-index: 99;
  background-color: #fff;
}
.action input{
  height: 30px;
  width: 100%;
  line-height: 30px;
  color: #333;
}
.action textarea{
  line-height: 30px;
  width: 100%;
  height: 90px;
  margin-top: 20px;
}
.action a{
  margin-top: 20px;
  display: inline-block;
  padding: 5px;
  background: rgb(38, 221, 253);
  cursor: pointer;
}
